2.亮牌：玩家将手上的牌公示给其他玩家。<br>\
3.比牌：庄家和闲家一一比较牌型大小，按照牌型大小顺序。<br>\
4.结算：庄家和闲家一一结算，根据闲家的下注数额及双方牌型计算输赢积分。<br>\
5.四张牌玩法：选择一张作为弃牌，其余三张组合牌型参与比牌。<br>\
6.限制降注：本局下注的最小分值要高于上一局下注分值的一半。<br>\
7.王癞玩法：扑克中将新增加大小王两张扑克，摸到大小王可以作为任意配牌和其他手牌进行组合，优先组合成最大牌型（注：大小王不参与比牌）。经典王癞：前两张手牌不会出现大小王。疯狂王癞：前两张手牌会出现大小王。<br>\
8.轮庄方式：<br>\
霸王庄：第一个坐下的玩家为庄家，如果离开，则第二个坐下的为庄家，直到游戏结束。不可负分时，玩家没分，游戏结束。<br>\
牌大坐庄：第一个坐下为庄家，第二局之后根据上一局牌型大的为庄家。"},
        "拼三张":{logo:"gameLogo4",video:"遊戲介紹_拚三張.mp4",content:"一、牌局规则<br>\
跟注：和上家加入同样的筹码。注：暗注相当于明注的2倍。<br>\
加注：加入比上家上手单注更多的筹码。加注后不能超过单注封顶。<br>\
看牌：查看自己三张牌的花色和点数，不能分别看一张或两张。<br>\
弃牌：指玩家自动弃权，本副牌认输且不收回本副牌筹码。<br>\
比牌：可选择其他未弃牌和未比牌输的玩家进行比牌，所有玩家看不到两人的牌面<br>\
二、牌型<br>\
豹子（炸弹）：三张点相同的牌。例：AAA、222。<br>\
顺金（同花顺、色托）：花色相同的顺子。例：黑桃456、红桃789。最大的顺金为花色相同的QKA，最小的顺金为花色相同的123。<br>\
金花（色皮）：花色相同，非顺子。例：黑桃368，方块145。<br>\
顺子（拖拉机）：花色不同的顺子。例：黑桃5红桃6方块7。最大的顺子为花色不同的QKA，最小的顺子为花色不同的123。<br>\
对子：两张点数相同的牌。例：223，334。<br>\
单张：三张牌不组成任何类型的牌。<br>\
底注：指游戏开始后每位玩家投入的初始注。<br>\
明注：看牌后的下注。暗注：不看牌的下注。注：暗注相当于明注的2倍。<br>\
满轮开牌：当前下注轮数达到开房设置的轮数上限时，以顺时针方向，从庄家的下一位起开牌，并依次比牌。<br>\
牌型的比较:豹子 > 顺金 > 金花 > 顺子 > 对子 > 单张<br>\
豹子、金花、对子、单张的比较，按照顺序比点的规则比较大小。 牌点从大到小为：A、K、Q、J、10、9、8、7、6、5、4、3、2，各花色不分大小；<br>\
顺金、顺子按照顺序比点。AKQ> KQJ>…>32A。<br>\
特殊牌型在普通时比较大小按单张牌型来算。当豹子存在时，特殊牌型 > 豹子。<br>\
同种牌型，对子时比对子的大小，其它牌型比最大的牌张，如最大牌张相同则比第二大的张，以此类推。当两牌型大小完全相同时，主动比牌者负。"},
        "百人龙虎":{logo:"gameLogo7",video:"遊戲介紹_百人龍虎.mp4",content:"基本规则<br>\
比牌：<br>\
1) 龙虎双方各发一张牌<br>\
2) 比牌只看龙虎双方的牌点数，不看花色：K>Q>J>10>9>……>2>A<br>\
3) 押中龙/虎，押注龙/虎的玩家获得1倍赔率<br>\
4) 押中和方（龙虎点数相同）则退回龙、虎两方的押注，押注和方的玩家获得8倍赔率。<br>\
坐庄规则：<br>\
分数大于桌子设置的上庄分数即可申请上庄，小于下庄分数，下一局将自动下庄。<br>\
下注限制：<br>\
下注筹码不能超自身积分数；<br>\
一个玩家单局在龙区或者虎区最多能下注的分数为底分×限红倍数，在和区域最多能下注为底分×限红倍数/10"},
        "百人牛牛":{logo:"gameLogo8",video:"遊戲介紹_百人牛牛.mp4",content:"基本规则<br>\
1.1牌库：<br>\
52张扑克牌去掉大小王<br>\
1.2牌型介绍：<br>\
玩家手牌中5张牌，任意3张相加起来得出的总数是10的整数即可；剩下的2张牌再次相加即为最后的点数，也就是牛几。最后2张牌相加只取个位数，若得出的数为2位数，则取个位数的点数。（最后2张牌相加为10或者20时默认为牛牛）<br>\
1.3特殊牌型：<br>\
同花顺：相同花色的顺，如全黑桃56789<br>\
五小牛：5张牌都小于5，且相加之和小于等于10，如AA223<br>\
五花牛：5张花牌，如JJQQK<br>\
炸弹牛：5张牌中有4张相同的牌，如55553<br>\
葫芦牛：3张相同的牌和2张相同的牌，如KKK22<br>\
同花牛：5张相同花色且不构成其他特殊牌型的牌，如黑桃69JJK<br>\
顺子牛：5张不同花色但点数相连的牌，如45678；注：10JQKA，算顺子牛<br>\
1.4牌型比较：<br>\
同花顺>五小牛>五花牛>炸弹牛>葫芦牛>同花牛＞顺子牛＞牛牛＞牛九＞牛八＞牛七＞牛六….无牛<br>\
牌型相同时比对最大的牌，K最大，A最小，最大的一张牌型相同，比花色，黑桃＞红桃＞梅花＞方片<br>\
一副牌满足多种牌型时，取最大的牌型<br>\
1.5牌型倍数：<br>\
同花顺（4倍），五小牛（4倍），五花牛（4倍），炸弹牛（4倍），葫芦牛（3倍），同花牛（3倍），顺子牛（3倍），牛牛（3倍），牛九（2倍），牛八（2倍），牛七至无牛（1倍）。<br>\
赔率为1-4倍。"},
      }
    }
  },
